import { wordSolver } from './word-solver.component.js';

describe('wordSolver', () => {

    it('Deve retornar as palavras que contenham as letras informadas', async () => {
        // Mock do dicionário
        const mockDictionary = ['apple', 'banana', 'grape', 'orange'];
        const loadDictionary = require('./word-solver.component.js').loadDictionary;
        loadDictionary.mockResolvedValue(mockDictionary);

        // Definir parâmetros de teste
        const params = { letters: 'ap' };

        // Chamar a função
        const results = await wordSolver(params, callback);

        // Verificar resultados
        expect(results).toEqual(['apple', 'grape']);
    });
});
